import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

export function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role='tabpanel'
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography style={{ whiteSpace: 'pre-line', fontSize: '1.2em' }}>
            {children}
          </Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

export default function VerticalTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <Typography
        sx={{
          marginTop: '100px',
          textAlign: 'center',
          fontSize: '4em',
          fontFamily: 'Roboto',
        }}
      >
          
      </Typography>
      <Box
        id='info'
        sx={{ flexGrow: 1, bgcolor: '#e7e6e6', display: 'flex', height: 225 }}
      >
        <Tabs
          orientation='vertical'
          variant='scrollable'
          value={value}
          onChange={handleChange}
          aria-label='Vertical tabs example'
          sx={{ borderRight: 1, borderColor: 'divider', width: '44vw' }}
        >
          <Tab label='' {...a11yProps(0)} sx={{ fontSize: '1.2em' }} />
          <Tab label='' {...a11yProps(1)} sx={{ fontSize: '1.2em' }} />
          <Tab
            label=' '
            {...a11yProps(2)}
            sx={{ fontSize: '1.2em' }}
          />
        </Tabs>
        <TabPanel value={value} index={0}>
          <Typography sx={{ fontSize: '1.1em', fontFamily: 'Roboto' }}>
                : . , .  17 5 
             17 51.    :  :  9:00 
            18:00 :  10:00  18:00 : .
          </Typography>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Typography sx={{ fontSize: '1.1em', fontFamily: 'Roboto' }}>
            Lorem ipsum dolor sit, amet consectetur adipisicing elit.
            Repudiandae qui iste consectetur officiis modi reiciendis numquam
            minus tempore! Id       
             30    ,    
             .
          </Typography>
        </TabPanel>
        <TabPanel value={value} index={2}>
          <Typography sx={{ fontSize: '1.1em', fontFamily: 'Roboto' }}>
            Lorem ipsum dolor sit, amet consectetur adipisicing elit.
            Repudiandae qui iste consectetur officiis modi reiciendis numquam
            minus tempore! Id : .
          </Typography>
        </TabPanel>
      </Box>
    </>
  );
}
