import React, { useState } from 'react';
import { FormControl } from '@mui/material';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';

const optionsRadius = [
  { id: { min: 0, max: 100 }, label: ' 100' },
  { id: { min: 100, max: 120 }, label: ' 100  120' },
  { id: { min: 120, max: 140 }, label: ' 120  140' },
  { id: { min: 140, max: 160 }, label: ' 140  160' },
  { id: { min: 160, max: 180 }, label: ' 160  180' },
  { id: { min: 180, max: Infinity }, label: ' 180' },
];

export default function SelectRad(props) {
  return (
    <FormControl sx={{ m: 1, minWidth: 120 }} size='small'>
      <InputLabel id='demo-select-small-label'></InputLabel>
      <Select
        labelId='demo-select-small-label'
        id='demo-select-small'
        value={props.radius}
        label='Age'
        onChange={props.changeRad}
        sx={{ width: '120px' }}
      >
        {optionsRadius.map(({ id, label }) => (
          <MenuItem key={label} value={id}>
            {label}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
